<template>
  <div class="container">
    <div class="content" v-loading="loading">
      <div class="btn-wrapper">
        <div>
          <h1 class="title">星云消息</h1><span>永不会被删除的消息平台</span>
        </div>
        <el-button type="primary" size="small" @click.native="$router.push({name: 'add'})">发一条</el-button>
      </div>
      <div v-for="item in list" :key="item.index" class="title-wrapper" @click="onItemClick(item.title)">
        <h1 class="title">{{item.title}}</h1>
      </div>
      <div class="empty" v-if="list.length === 0">
        <p>还没有消息，发一条吧</p>
      </div>
    </div>
    <footer class="footer">
      <div>© 2018 <a href="https://nebulas.io/">星云链智能合约</a> 强力驱动</div>
      <div>
        <a href="https://github.com/GeoffZhu/nebulas-message" target="_blank">Github</a>
      </div>
    </footer>
  </div>
</template>

<script>
import { getDialyList } from '../api'

export default {
  name: 'list',
  data () {
    return {
      loading: true,
      list: []
    }
  },
  async created () {
    this.list = await getDialyList()
    this.loading = false
  },
  methods: {
    onItemClick (title) {
      this.$router.push({
        path: 'detail',
        query: {
          title
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/styles/list.less';
.btn-wrapper {
  display: flex;
  justify-content: space-between;
  padding-bottom: 15px;
  border-bottom: 1px solid #e6e6e6;
  .title {
    display: inline-block;
    vertical-align: middle;
    font-size: 18px;
    padding-bottom: 0;
    margin-top: 0;
    margin-bottom: 0;
    border-bottom: none;
    line-height: 26px;
  }
  span {
    display: inline-block;
    font-weight: normal;
    font-size: 14px;
    margin-left: 10px;
    line-height: 26px;
    position: relative;
    top: 3px;
  }
}
.empty {
  text-align: center;
  padding: 80px;
}
</style>



