<template>
  <div class="container">
    <div class="content" v-loading="loading">
      <el-input v-model="title" placeholder="请输入标题" :disabled="!disabled"></el-input>
      <el-input
        class="mt10"
        type="textarea"
        :rows="10"
        placeholder="请输入内容"
        :disabled="!disabled"
        v-model="content">
      </el-input>
      <div class="btn-wrapper">
        <el-button class="ml10" type="primary" @click.native="onClickDeploy" :disabled="disabled">发布</el-button>
        <el-button type="primary" @click.native="onClickTestDeploy" :disabled="!disabled">计算发布需花费GAS</el-button>
        <p class="hash" v-if="txhash" ><i class="el-icon-warning mr10"></i>txhash:{{txhash}}</p>
      </div>
    </div>
    <footer class="footer">
      <div>© 2018 <a href="https://nebulas.io/">星云链智能合约</a> 强力驱动</div>
      <div>
        <a href="https://github.com/GeoffZhu/nebulas-message" target="_blank">Github</a>
      </div>
    </footer>
  </div>
</template>

<script>
import { setDiaryTest, setDiary } from '../api'
export default {
  name: 'add',
  data () {
    return {
      loading: false,
      disabled: true,
      estimateGas: 0,
      title: '',
      content: '',
      txhash: ''
    }
  },
  methods: {
    async onClickTestDeploy () {
      this.loading = true
      let resp = await setDiaryTest(this.title, this.content)
      this.loading = false
      if (resp.result === '""') {
        this.$message({
          message: `测试成功，发布需要花费GAS ${resp.estimate_gas}`,
          type: 'success'
        })
        this.estimateGas = resp.estimate_gas
        this.disabled = false
      }
    },
    async onClickDeploy () {
      this.loading = true
      try {
        let resp = await setDiary(this.title, this.content, this.estimateGas)
        this.$message({
          message: `发布成功，等待区块接受中...`,
          type: 'success'
        })
        this.txhash = resp.txhash
        this.$alert(`
          发布成功，需要等到星云链主网接受本次交易后，才会在列表页中显示出来。
          `, '安全，放心', {
          confirmButtonText: 'OK'
        })
      } catch (err) {
        console.error(err)
        this.$message({
          message: `发布失败，请检查您的是否有足够的余额支付手续费（GAS）`,
          type: 'error'
        })
      }
      this.loading = false
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/styles/list.less';
.btn-wrapper {
  display: flex;
  flex-direction: row-reverse;
  margin-top: 10px;
  .hash {
    font-size: 14px;
    line-height: 42px;
    margin-bottom: 0px;
    margin-right: 20px;
  }
}
</style>



