// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import '@/assets/styles/element-ui/index.css'
import '@/assets/styles/reset.css'
import '@/assets/styles/common.css'
import '@/assets/styles/content.less'

import Vue from 'vue'
import ElementUI, { MessageBox } from 'element-ui'
import App from './App'
import router from './router'
import * as filters from './utils/filter'
import { getGasPrice } from './api'

Vue.config.productionTip = false

Vue.use(ElementUI)

Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})

window.store = {
  contractAdddress: 'n1mVyXb1DswgimoRUJsLER4JDDHKiuvgJgn'
}

getGasPrice().then(resp => {
  store.gasPrice = resp['gas_price']
  /* eslint-disable no-new */
  new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
  })
}).catch(err => {
  console.error(err)
  MessageBox('网络错误，请检查您的网络', '错误', {
    confirmButtonText: '确定'
  })
})



