<template>
  <div class="container">
    <img class="banner" src="http://o80ronwlu.bkt.clouddn.com/banner.jpg" alt="banner">
    <el-button type="primary" class="unlock-btn" @click.native="onUnlockClick">使用您的 钱包文件 解锁星云消息</el-button>
    <input ref="key-file-input" type="file" class="c-hide" @change="handkeFileChange">
    <el-dialog title="请输入密码" :visible.sync="dialogVisible" center>
      <div class="dialog-wrapper">
        <div class="line">
          <el-input v-model="password" auto-complete="off" class="password-input" type="password"></el-input>
        </div>
        <div class="line mt10">
          <el-button type="primary" @click.native="onConfirmClick">解锁</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { readAsText } from 'promise-file-reader'
export default {
  name: 'locked',
  data () {
    return {
      filename: '',
      $rawFile: null,
      dialogVisible: false,
      password: ''
    }
  },
  async created () {},
  methods: {
    onUnlockClick () {
      this.$refs['key-file-input'].click()
    },
    async onConfirmClick () {
      if (this.password) {
        try {
          store.account.fromKey(store.fileJson, this.password)
          this.dialogVisible = false
          this.$router.push({name: 'list'})
        } catch (err) {
          console.error(err)
          this.$message({
            type: 'error',
            message: '密码错误'
          })
        }
      } else {
        this.$message('请输入密码')
      }
    },
    handkeFileChange (e) {
      this.$rawFile = e.target.files[0]
      this.filename = e.target.files[0].name
      readAsText(this.$rawFile).then(text => {
        try {
          store.fileJson = JSON.parse(text)
          store.account = Account.fromAddress(store.fileJson.address)
          this.$message({
            type: 'warning',
            message: `正在解锁中...`,
            onClose: () => {
              this.dialogVisible = true
            }
          })
        } catch (err) {
          console.error(err)
          this.$message({
            type: 'error',
            message: '请确认您选择是钱包文件'
          })
        }
      }).catch(err => console.error(err))
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  min-height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.banner {
  height: 300px;
  width: 300px;
  margin-top: 60px;
  border-radius: 10px;
}
.unlock-btn {
  width: 400px;
  margin-top: 50px;
}
.password-input {
  width: 300px;
}
.dialog-wrapper {
  text-align: center;
}
</style>



