<template>
  <div class="container">
    <div class="content" v-loading="loading">
      <h1>{{title}}</h1>
      <p>{{content}}</p>
    </div>
    <footer class="footer">
      <div>© 2018 <a href="https://nebulas.io/">星云链智能合约</a> 强力驱动</div>
      <div>
        <a href="https://github.com/GeoffZhu/nebulas-message" target="_blank">Github</a>
      </div>
    </footer>
  </div>
</template>

<script>
import { getDialyDetail } from '../api'
export default {
  name: 'detail',
  data () {
    return {
      loading: true,
      title: '',
      content: ''
    }
  },
  async created () {
    let title = this.$route.query.title
    let resp = await getDialyDetail(title)
    this.title = resp.title
    this.content = resp.content
    this.loading = false
  },
  methods: {
  }
}
</script>

<style lang="less" scoped>
@import '../assets/styles/list.less';
</style>



