export async function getAccountInfo () {
  let accountState = await neb.api.getAccountState(store.account.getAddressString())
  accountState.balanceNas = Unit.fromBasic(accountState.balance, 'nas')
  return accountState
}

export function getGasPrice () {
  return neb.api.gasPrice()
}

export async function getDialyList () {
  let accountInfo = await getAccountInfo()
  return neb.api.call({
    from: store.account.getAddressString(),
    to: store.contractAdddress,
    value: 0,
    nonce: accountInfo.nonce,
    gasPrice: store.gasPrice,
    gasLimit: '200000',
    contract: {
      function: 'getDiaryList',
      args: JSON.stringify([100, 0])
    }
  }).then(resp => JSON.parse(resp.result))
}

export async function getDialyDetail (title) {
  let accountInfo = await getAccountInfo()
  return neb.api.call({
    from: store.account.getAddressString(),
    to: store.contractAdddress,
    value: 0,
    nonce: accountInfo.nonce,
    gasPrice: store.gasPrice,
    gasLimit: '200000',
    contract: {
      function: 'getDiary',
      args: JSON.stringify([title])
    }
  }).then(resp => JSON.parse(resp.result))
}

export async function setDiaryTest (title, content) {
  let accountInfo = await getAccountInfo()
  return neb.api.call({
    from: store.account.getAddressString(),
    to: store.contractAdddress,
    value: 0,
    nonce: Number(accountInfo.nonce) + 1,
    gasPrice: store.gasPrice,
    gasLimit: '200000',
    contract: {
      function: 'setDiary',
      args: JSON.stringify([{
        title,
        content
      }])
    }
  })
}

export async function setDiary (title, content, gas = '200000') {
  let accountInfo = await getAccountInfo()
  const apiList = [
    { chainId: 1, name: 'Mainnet', url: 'https://mainnet.nebulas.io' },
    { chainId: 1001, name: 'Testnet', url: 'https://testnet.nebulas.io' },
    { chainId: 100, name: 'Local Nodes', url: 'http://127.0.0.1:8685' }
  ]
  let gTx = new Transaction(
    apiList[0].chainId,
    store.account,
    store.contractAdddress,
    '0', Number(accountInfo.nonce) + 1, Number(store.gasPrice), gas, {
      function: 'setDiary',
      args: JSON.stringify([{
        title,
        content
      }])
    }
  )
  // args: """
  // source:""
  // sourceType:"js"
  gTx.signTransaction()
  return neb.api.sendRawTransaction(gTx.toProtoString())
}



